@import url('https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap');

.navBar {
    display: flex;
    position: fixed;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    font-family: 'Bebas Neue', cursive;

    height: 55px;

    font-size: 45px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.22;
    letter-spacing: normal;
    text-align: left;

    color: #ECEDED;
    background-color: rgba(100, 100, 100, 0.63);
    z-index: 1;
}

.navBar ul {
    width: 50%;
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    font-size: 1.7rem;
    margin-top: 30px;
    margin-bottom: 19px;
    margin-right: 40px;
    padding-inline-start: 0;
}

.navBar li button {
    background: transparent;
    border: none;
    color: #ECEDED;
    
    font-family: 'Bebas Neue', cursive;
    font-size: 25px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.22;
    letter-spacing: normal;
    text-align: left;
    
    text-decoration: none;
    text-align: center;
    cursor: pointer;
}

.navBar li button:hover {
    color: #ECEDED;
    text-decoration: none;
}

.firstLogo {
    cursor: pointer;
    margin-left: 15px;
    margin-top: 17.5px;
}

.littleMargin {
    margin-top: 7px;
}

.icons {
    color: white;
    width: 30px;
    height: 30px;
    object-fit: contain;
}

.mediaMob {
    background: transparent;
    border: 0;
    cursor: pointer;
}

@media only screen and (max-width: 768px) {

    /* For mobile phones: */
    .mediaMob {
        background: transparent;
        border: 0;
        cursor: pointer;
    }

    .mediaDesk {
        display: none;
    }

    .navBar ul {
        justify-content: flex-end;
        margin-right: 15px;
    }
}

@media only screen and (orientation: portrait) {

    /* For mobile phones: */
    .mediaMob {
        background: transparent;
        border: 0;
        cursor: pointer;
    }

    .mediaDesk {
        display: none;
    }

    .navBar ul {
        justify-content: flex-end;
        margin: 17.5px;
        margin-right: 15px;
    }
}

@media only screen and (max-height: 550px) and (orientation: landscape) {

    /* For mobile phones: */
    .mediaMob {
        background: transparent;
        border: 0;
        cursor: pointer;
    }

    .mediaDesk {
        display: none;
    }

    .navBar ul {
        justify-content: flex-end;
        margin: 17.5px;
        margin-right: 15px;
    }

}

@media only screen and (min-height: 550px) and (min-width: 769px) {

    /* For desktop: */
    .mediaMob {
        background: transparent;
        border: 0;
        cursor: pointer;
        display: none;
    }


}